.fg {
  position: relative;
  margin: 0;
  input {
    border: none;
    padding: 0 1.09em;
    height: 2.5em;
    width: 100%;
    font-size: 2em;
    background: rgba(125, 127, 146, 0.25);
    color: #fff;
    border-radius: .3125em;
    &::placeholder {
      color: $muted;
    }
    &:focus{
      background: rgba(125, 127, 146, 0.35);
    }
  }
  &__icon{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 2.1875em;
    width: 1.625em;
    &~input{
      padding-left: 2.4em;
    }
  }
}

.check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        // background-color: $green;
        border: 3px solid #fff;
      }
    }
    &:disabled+label {
      color: #AAAAAA;
      pointer-events: none;
    }
  }
  label {
    padding-left: 40px;
    position: relative;
    min-height: 20px;
    font-size: 14px;
    line-height: 20px;
    cursor: pointer;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      border-radius: 2px;
      background: rgba(255, 255, 255, 0.01);
      border: 1px solid rgba(255, 255, 255, 0.45);
      width: 20px;
      height: 20px;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      transition: all .100s;
    }
  }
}


@media screen and (max-width: 992px){
  .fg{
    input{
      font-size: 1em;
      padding: 0 1.25em;
      height: 3.3125em;
      border-radius: .625em;
    }
    &__icon{
      width: 1em;
      left: 1.25em;
      &~input{
        padding-left: 2.875em;
      }
    }
  }
}
