.collection{
  padding: 5.3125em 0;
  overflow: hidden;
  &__top{

  }
}


.films-slider2{
  margin-top: 1.75em;
  &:first-child{
    margin-top: 2.25em;
  }
  .swiper{
    &-container{
      width: 34.375em;
    }
    &-slide{
      padding-right: 2.5em;
      &-active{
        .film-card{
          &__img{
            &:after{
              opacity: 1;
            }
          }
        }
      }
    }
  }
  .film-card{
    &__badge,
    &__img{
      &:before{
        display: none;
      }
    }
    &__badge{
      height: 2.75em;
    }
    &__content{
      display: block;
    }
  }
  &._type2{
    .swiper{
      &-container{
        width: 17.4375em;
      }
      &-slide{
        padding-right: 2.25em;
      }
    }
    .film-card{
      &__badge{
        top: 1.25em;
        left: -.9375em;
        height: 1.9375em;
        padding: 0 .625em;
        min-width: unset;
        span{
          font-size: 1em;
        }
      }
      &__img{
        padding-bottom: 144%;
      }
      &__desc{
        font-size: 1.125em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .collection{
    padding: 0;
  }

  .films-slider2{
    margin-top: 2.25em;
    &:first-child{
      margin-top: 1.375em;
    }
    .swiper{
      &-container{
        width: 100%;
      }
      &-slide{
        width: 15.9375em;
        padding-right: .9375em;
      }
    }
    &._type2{
      .swiper {
        &-container {
          width: 100%;
        }
        &-slide{
          width: 9.6875em;
          padding-right: .9375em;
        }
      }
      .film-card{
        &__desc{
          font-size: .875em;
        }
      }
    }
  }
}
