.settings{
  padding-top: 3em;
  &__title{
    &-view2{
      display: none;
    }
  }
  &__grid{
    display: flex;
    justify-content: space-between;
    margin-top: 2em;
  }
  &__sidebar{
    width: 25em;
  }
  &__content{
    width: calc(100% - 29.375em);
  }
  &__buttons{
    position: sticky;
    top: 1em;
    @media screen and (min-width: 993px){
      display: block !important;
    }
  }
  &__btn{
    justify-content: unset;
    width: 100%;
    height: 5em;
    padding: 0 2.1875em;
    color: #FFFFFF;
    border-radius: .625em;
    @include lastMb(1.25em);
    span{
      font-size: 2em;
    }
    &:before{
      border: none;
    }
    &:hover{
      background: $accent;
    }
    @media screen and (min-width: 993px){
      &._active{
        background: $accent;
      }
    }
  }
  &__view{
    display: none;
    @media screen and (min-width: 993px){
      &._active{
        display: block;
      }
    }
  }
}

.setting-card{
  @include lastMb(1.25em);
  padding: 1.125em 2.1875em 1.25em;
  border-radius: .625em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  transition: all .3s;
  &:before{
    content: '';
    position: absolute;
    inset: 0;
    background: rgba(125, 127, 146, 0.25);
    backdrop-filter: blur(5px);
    border-radius: inherit;
    z-index: -1;
  }
  &:hover{
    cursor: pointer;
    &:before{
      background: $accent;
    }
  }
  &__desc{
    margin-top: .25em;
  }
  &__check{
    flex-shrink: 0;
    input{
      position: absolute;
      opacity: 0;
      inset: 0;
      z-index: 2;
      cursor: pointer;
      &:checked+label{
        opacity: 1;
      }
    }
    label{
      display: block;
      width: 3.3125em;
      height: 3.125em;
      background-image: url(../img/check.svg);
      opacity: 0;
      transition: all .1s;
      @include bgImgSize(100%);
    }
  }
  &__text{
    text-align: right;
  }
  &__status{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-top: .625em;
    img{
      width: 1.125em;
      margin-right: .625em;
      flex-shrink: 0;
    }
  }
}


@media screen and (max-width: 992px){
  .settings{
    padding: 0;
    &._open-detail{
      .settings__title-view2{
        display: inline;
      }
    }
    &__title{
      text-align: center;
    }
    &__grid{
      display: block;
      margin-top: 3em;
    }
    &__sidebar,
    &__content{
      width: 100%;
    }
    &__btn{
      @include lastMb(.625em);
      padding: 0 1.25em;
      height: 3.3125em;
      font-weight: 700;
      justify-content: space-between;

      span{
        font-size: 1.125em;
      }
    }
  }

  .settings__btn,
  .setting-card2,
  .setting-card3{
    &:after{
      content: '';
      flex-shrink: 0;
      width: .8125em;
      height: 1.5em;
      background-image: url(../img/arrow-right.svg);
      @include bgImgSize(100%);
      position: relative;
      z-index: 2;
    }
  }

  .setting-card{
    padding: .875em 1.25em;
    &__title{
      font-size: 1.125em;
    }
    &__text,
    &__desc{
      font-size: 1em;
    }
    &__check{
      label{
        width: 1.9375em;
        height: 1.5em;
      }
    }
    &__status{
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 3em;
      justify-content: center;
      margin: 0;
      width: calc(100% - 8.125em - 4.25em);
      img{
        width: .75em;
        margin-right: .3125em;
      }
      span{
        font-size: 1em;
      }
    }
  }

  .setting-card3,
  .setting-card2{
    padding-right: 3em;
    display: block;
    &:after{
      position: absolute;
      right: 1.25em;
      top: 50%;
      transform: translateY(-50%);
    }
    .setting-card__desc{
      display: none;
    }
    .setting-card__text{
      text-align: left;
      margin-top: .25em;
    }
  }

  .setting-card3{
    .setting-card__connect{
      width: 8.125em;
    }
  }
}
