.filter{
  padding: 3em 0;
  &__grid{
    margin-top: 1.75em;
    display: flex;
    justify-content: space-between;
  }
  &__sidebar{
    width: 32.5em;
  }
  &__content{
    width: calc(100% - 35em);
  }
  &__toggle{
    display: none;
  }
  @media screen and (min-width: 993px){
    &__sidebar{
      display:  block !important;
    }
    .search__items{
      margin-top: -1.5625em;
      width: calc(100% + 1.875em);
    }
    .film-card{
      width: calc(100% / 4 - 1.875em);
      margin-right: 1.875em;
    }
  }
}

.filter-btn{
  @include lastMb(1.25em);
  padding: .875em 2.1875em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: all .3s;
  cursor: pointer;
  color: #fff;
  border-radius: .625em;
  &:before{
    border: none;
  }
  &:after{
    content: '';
    flex-shrink: 0;
    width: .9375em;
    height: 1.5em;
    background-image: url(../img/arrow-right.svg);
    @include bgImgSize(100%);
    position: relative;
    z-index: 2;
  }
  &__content{
    position: relative;
    z-index: 2;
  }
  &__desc{
    font-size: 1.25em;
    line-height: 137.5%;
  }
}

.selected-filters{
  display: none;
}


@media screen and (max-width: 992px){
  .filter{
    padding: 0;
    &__top{
      padding: 0;
      position: relative;
    }
    &__toggle{
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      width: 1.625em;
      display: flex;
      img{
        width: 100%;
        display: block;
      }
      .pin{
        right: -.3125em;
        top: -.125em;
      }
    }
    &__title{
      text-align: center;
    }
    &__grid{
      margin-top: 3em;
      display: block;
    }
    &__sidebar,
    &__content{
      width: 100%;
    }
    &__sidebar{
      display: none;
      margin-bottom: 1.875em;
    }
    &__content{
      margin-top: -1.875em;
    }
    .search__items{
      margin: 0 !important;
    }
  }

  .filter-btn{
    padding-left: 1.25em;
    padding-right: 1.25em;
    @include lastMb(.625em);
    &__desc{
      font-size: 1em;
    }
  }

  .selected-filters{
    display: flex;
    flex-wrap: wrap;
    margin-top: 1.25em;
    width: calc(100% + .5em);
    &__item{
      margin: .5em .5em 0 0;
      height: 1.9375em;
      padding: 0 .625em;
      border-radius: .625em;
      &:before{
        border: none;
      }
      span{
        font-size: 1em;
      }
      img{
        width: .625em;
        margin-left: .625em;
        flex-shrink: 0;
        display: block;
      }
    }
  }
}
