.episodes-page{
  position: relative;
  &__grid{
    padding: 5.625em 0 2.75em;
    display: flex;
    justify-content: space-between;
    height: $fullHeight;
  }
  &__col{
    height: 100%;
    &:nth-child(1){
      width: calc(100% - 65em);
      display: flex;
      flex-direction: column;
    }
    &:nth-child(2){
      width: 64em;
      padding-top: 5.625em;
    }
  }
}

.episodes-settings{
  &__buttons{
    display: flex;
    flex-wrap: wrap;
  }
  &__btn{
    margin: .625em 1.25em 0 0;
    height: 3.75em;
  }
  @media screen and (min-width: 993px){
    overflow-y: auto;
    overflow-x: hidden;
    min-height: 1em;
    flex-grow: 1;
    margin-bottom: 2em;
    @include scrollNull;
    .swiper{
      &-wrapper{
        display: block;
        transform: unset !important;
      }
      &-slide{
        width: 100% !important;
        @include lastMb(1.875em);
      }
      &-scrollbar{
        display: none;
      }
    }
  }
}

.episode-play{
  margin-top: auto;
  padding-top: 2em;
  .film-detail__props{
    margin: 0 0 1.5em;
  }
  .episode-progress{
    margin-top: 1.5em;
    max-width: 26.875em;
  }
  &__name{
    margin-top: .75em;
  }
}

.episode-progress{
  height: .25em;
  width: 100%;
  background: rgba(255, 255, 255, 0.35);
  border-radius: .5em;
  position: relative;
  span{
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    max-width: 100%;
    border-radius: inherit;
    background: linear-gradient(#7F71DA 10%, #6757CE 100%);
  }
}

.episodes{
  padding-right: 5em;
  padding-bottom: 2em;
  overflow-y: auto;
  height: calc(100% + 2.75em);
  @include scroll;
}

.episode-card,
.torrent-card{
  @include lastMb(2.5em);
  background: #000000;
  border-radius: .9375em;
  max-width: calc(100% - 4.75em);
  margin-left: auto;
  margin-right: auto;
  cursor: pointer;
  &._watched{
    background: rgba(255, 255, 255, 0.15);
  }
  &._active {
    background: $accent;
    max-width: 100%;
  }
}

.episode-card{
  display: flex;
  &._watched{
    .episode-card{
      &__img{
        opacity: .5;
      }
    }
  }
  &._active{
    .episode-progress{
      background: rgba(255, 255, 255, 0.35);
      span{
        background: #fff;
      }
    }
    .episode-card__img{
      width: 21.125em;
      height: 12.1875em;
    }
    .episode-card__content{
      width: calc(100% - 21.125em);
    }
    .episode-card__title,
    .episode-card__quality{
      font-size: 1.625em;
      line-height: 135%;
    }
    .episode-card__status{
      font-size: 1.25em;
    }
  }
  &__img{
    width: 18.75em;
    height: 11.25em;
    flex-shrink: 0;
    img{
      border-radius: .9375em 0 0 .9375em;
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
  }
  &__content{
    width: calc(100% - 18.75em);
    padding: 2.25em 2.5em;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
  }
  &__title{
    letter-spacing: 0.03em;
  }
  &__eta{
    letter-spacing: 0.03em;
  }
  .episode-progress{
    margin-top: 1em;
    height: .3125em;
  }
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 1.375em;
    margin-top: auto;
  }
}

.torrent-card{
  padding: 1.375em 2.5em;
  min-height: 13.75em;
  display: flex;
  flex-direction: column;
  &._active{
    .torrent-card__size,
    .torrent-card__title{
      font-size: 1.625em;
    }
    .torrent-card__item{
      &-img{
        width: 1.625em;
      }
      span{
        font-size: 1.4375em;
      }
    }
  }
  &__bottom{
    margin-top: auto;
    padding-top: 1.125em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__items{
    display: flex;
    align-items: center;
  }
  &__item{
    display: flex;
    align-items: center;
    font-weight: 600;
    @include lastMr(1.875em);
    img{
      width: 1.4375em;
      margin-right: .5em;
      flex-shrink: 0;
    }
    span{
      font-size: 1.3125em;
      line-height: 138%;
    }
  }
}


@media screen and (max-width: 992px){
  .episodes-page{
    overflow: hidden;
    &__grid{
      padding: 0 0 11.875em;
      height: auto;
      display: block;
      position: relative;
    }
    &__col{
      &:nth-child(1){
        width: 100%;
        overflow: unset;
        display: block;
      }
      &:nth-child(2){
        width: 100%;
        padding: 3.125em 0 1.25em;
      }
    }
  }

  .episodes-settings{
    padding-bottom: 2em;
    position: relative;
    .swiper{
      &-slide{
        width: unset;
        padding-right: 1.5625em;
        min-width: 8.3125em;
      }
      &-scrollbar{
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
      }
    }
    &__buttons{
      margin: 0;
      flex-direction: column;
      align-items: flex-start;
    }
    &__btn{
      margin: .75em 0 0;
      height: 2.75em;
      padding: 0 1.875em;
      span{
        font-size: 1.25em;
      }
    }
  }

  .episode-play{
    padding: 0;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    .film-detail__props{
      margin-bottom: 1.125em;
    }
  }

  .episodes{
    overflow: unset;
    padding: 0;
    height: unset;
  }

  .episode-card,
  .torrent-card{
    max-width: unset;
    width: 100%;
    @include lastMb(1.25em);
    &._active{
      max-width: unset;
      width: calc(100% + 1.25em);
      margin-left: -.625em;
    }
  }

  .episode-card{
    &._active{
      .episode-card__img{
        width: 15em;
      }
      .episode-card__content{
        width: calc(100% - 15em);
      }
    }
    &__img{
      width: 12.5em;
    }
    &__content{
      padding: 1.25em;
      width: calc(100% - 12.5em);
    }
  }

  .torrent-card{
    padding: 1em;
  }
}


@media screen and (max-width: 576px){
  .episode-card{
    &._active{
      .episode-card{
        &__img{
          width: 6.875em;
          height: 8.125em;
        }
        &__content{
          width: calc(100% - 6.875em);
        }
        &__title,
        &__quality{
          font-size: 1.25em;
        }
        &__status,
        &__eta{
          font-size: 1em;
        }
      }
    }
    &__img{
      width: 5.3125em;
      height: 7.5em;
    }
    &__content{
      padding: .625em;
      width: calc(100% - 5.3125em);
    }
    &__bottom{
      padding: 0;
      margin-top: .375em;
      flex-direction: column;
    }
    &__quality{
      margin-top: .25em;
    }
  }

  .torrent-card{
    min-height: 15.625em;
    &._active{
      .torrent-card__title{
        font-size: 1.25em;
      }
      .torrent-card__size{
        font-size: 1.125em;
      }
      .torrent-card__item{
        img{
          width: 1.1875em;
          margin-right: .25em;
        }
        span{
          font-size: 1em;
        }
      }
    }
    &__bottom{
      flex-direction: column;
    }
    &__item{
      @include lastMr(.9375em);
      span{
        font-size: .875em;
      }
      img{
        width: 1em;
      }
    }
    &__size{
      margin-top: .8em;
    }
  }

  .torrent-page{
    .episodes-page__grid{
      padding-bottom: 8em;
    }
    .episode-play{
      text-align: center;
      .film-detail__props{
        justify-content: center;
      }
    }
  }
}
