.search{
  padding: 3em 0;
  overflow: hidden;
  min-height: $fullHeight;
  display: flex;
  flex-direction: column;
  .container{
    flex-grow: 1;
    display: flex;
    flex-direction: column;
  }
  &__items{
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + 2.5em);
    .film-card{
      width: calc(100% / 6 - 2.5em);
      margin: 1.5625em 2.5em 0 0;
    }
  }
  &__keyboard{
    padding-top: 1em;
    margin-top: auto;
  }
}

.search-nav{
  display: flex;
  margin-top: 1.75em;
  &__field{
    flex-grow: 1;
    margin: 0 2em;
  }
  &__voice,
  &__filter{
    flex-shrink: 0;
    @include center;
    img{
      width: 100%;
      display: block;
    }
    &:hover{
      opacity: .8;
    }
  }
  &__voice{
    width: 2.1875em;
  }
  &__filter{
    width: 2.4375em;
  }
}

.search-story{
  display: flex;
  flex-wrap: wrap;
  margin-top: .9375em;
  width: calc(100% + 1.25em);
  &__item{
    margin: 1.25em 1.25em 0 0;
    height: 3.875em;
    border-radius: .625em;
    &:before{
      border: none;
    }
    img{
      width: 1.625em;
      margin-right: 1.25em;
    }
  }
  &__nav{
    width: 100%;
    margin-top: .625em;
  }
  &__clear{
    padding: 0 .9375em;
  }
}

.keyboard{
  &__row{
    width: calc(100% + .625em);
    display: flex;
    margin-top: .625em;
    &._end{
      justify-content: flex-end;
    }
  }
  &__btn{
    width: calc(100% / 12 - .625em);
    margin-right: .625em;
    height: 6.25em;
    border-radius: .625em;
    cursor: pointer;
    &._double{
      width: calc(100% / 6 - .625em);
    }
    &._enter{
      width: calc(100% / 7 - .625em);
    }
    &._hexad{
      width: calc(100% / 2 - .625em);
      img{
        height: 1.375em;
      }
    }
    &:before{
      border: none;
    }
    span{
      font-size: 3em;
      line-height: 136%;
    }
    img{
      height: 2.375em;
      width: auto;
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .search{
    padding: 0;
    &__title{
      text-align: center;
    }
    &__items{
      width: calc(100% + 1.25em);
      .film-card{
        width: calc(100% / 3 - 1.25em);
        margin: 1.875em 1.25em 0 0;
      }
    }
  }

  .search-nav{
    margin-top: 1.875em;
    &__field{
      margin: 0 .5em;
      input{
        font-size: 1.125em;
        padding-left: 2.55em;
        padding-right: 1em;
      }
    }
    &__voice{
      width: 1.5em;
    }
    &__filter{
      width: 1.625em;
    }
  }

  .search-story{
    margin-top: 1.25em;
    &__item{
      margin: .625em .625em 0 0;
      padding: 0 1.25em;
      height: 3.625em;
      img{
        width: 1.375em;
        margin-right: .5em;
      }
    }
    &__nav{
      margin-top: .3125em;
    }
    &__clear{
      padding: 0 .625em;
      height: 3em;
      img{
        width: 1.25em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .search{
    &__items{
      width: calc(100% + .75em);
      .film-card{
        width: calc(100% / 2 - .75em);
        margin: 1.875em .75em 0 0;
      }
    }
  }
}
